@import url("https://fonts.googleapis.com/css2?family=Gothic+A1:wght@500&family=Lato:wght@400;700&family=Roboto:wght@300;400;500;700;900&display=swap");

.item-card {
    width: 82%;
    aspect-ratio: 2/3;
    margin-bottom: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    border-radius: 5px;
    box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.263);
    font-family: "Roboto", sans-serif;
    cursor: pointer;
    background-color: rgb(245, 245, 245);
}

.item-img-container {
    width: 100%;
    height: 80%;
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

.item-info-container {
    width: 100%;
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border-top: 1px solid rgb(205, 205, 205); */
    background-color: rgb(254, 254, 254);
}
.item-img {
    width: 100%;
    aspect-ratio: auto;
    align-self: center;
}
p {
    margin: 0;
    font-size: 1.2em;
}

.item-price {
    color: rgb(171, 171, 171);
    font-size: 0.9em;
    line-height: 2em;
}
