#cart-content {
    width: 100vw;
    max-width: 100%;
    margin-top: 100px;
}
#cart {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    align-items: center;
    justify-items: center;
}

@media (max-width: 1050px) {
    #cart {
        grid-template-columns: 100%;
    }
}

#buy-cart-btn {
    margin-top: 30px;
    margin-left: 50vw;
    margin-bottom: 50px;
    transform: translate(-50%);
    background-color: white;
    border: 1px solid black;
    padding: 8px 20px;
    font-family: "Lato", sans-serif;
    font-size: 16px;
}

hr {
    width: 95vw;
    margin-left: 2.5vw;
    margin-top: 40px;
    border: none;
    border-top: 1px solid #bebebe;
}

#empty-cart-notice {
    position: absolute;
    top: 47vh;
    left: 50vw;
    transform: translate(-50%, -50%);
}

#cart-buy-loader {
    border: 5px solid rgb(168, 168, 168);
    border-top: 5px solid rgb(229, 229, 229);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 2s ease-in-out infinite;
    z-index: inherit;
    display: none;
}

#loader-background {
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: rgba(79, 79, 79, 0.073);
    z-index: 5;
}

@keyframes spin {
    0% {
        rotate: 0;
    }
    100% {
        rotate: 360deg;
    }
}
