#menu-btn {
    position: absolute;
    right: 60px;
    cursor: pointer;
}

#menu {
    position: absolute;
    top: 60px;
    right: 50px;
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    list-style: none;
    background-color: white;
    padding: 5px;
    border-radius: 5px;
}

#menu > li {
    display: flex;
    margin: 3px;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

#menu > li > svg {
    margin-left: 5px;
}
