#search-bar {
    width: clamp(200px, 33%, 600px);
    margin-left: calc(25vw - 100px);
    position: relative;
    padding: 0px 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    border: 1px solid black;
}

@media (max-width: 860px) {
    #search-bar {
        width: calc(95% - 115px);
        margin-left: 5%;
    }
}

#categories-btn {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

#categories-container {
    position: absolute;
    top: 35px;
    transform: translate(-15%);
    padding: 10px;
    list-style: none;
    background-color: white;
    border-radius: 5px;
}
#categories-container > li {
    margin: 0;
    cursor: pointer;
}
#search-input {
    border: none;
    outline: none;
    padding: 5px;
    margin-left: 5px;
    width: calc(100% - 67px);
    font-size: 1.1em;
}
#search-btn {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.unselectable {
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    user-select: none;
}
