@import url("https://fonts.googleapis.com/css2?family=Gothic+A1:wght@500&family=Lato:wght@400;700&family=Roboto:wght@300;400;500;700;900&display=swap");

#nav-bar {
    width: 100vw;
    max-width: 100%;
    position: fixed;
    top: 0;
    margin-top: 0;
    padding: 15px;
    display: flex;
    align-items: center;
    list-style: none;
    background-color: white;
    font-family: "Lato", sans-serif;
    z-index: 2;
}

#title {
    height: 100%;
    line-height: 100%;
    margin-left: 10px;
    margin-right: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 35px;
    cursor: pointer;
}

#title > span {
    vertical-align: middle;
}

#nav-bar-right {
    width: calc(35vw - 100px);
    display: grid;
    grid-template-columns: 80% 20%;
}

#cart-btn {
    width: fit-content;
    margin-left: 40%;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
#cart-btn > svg {
    margin-left: 5px;
    margin-top: 2px;
}

#log-btn {
    color: black;
    justify-self: right;
}
