#item-container {
    width: 100vw;
    height: 80vh;
    max-width: 100%;
    margin-top: 100px;
    display: flex;
}
#item-img {
    width: 45%;
    height: fit-content;
    max-height: 100%;
    object-fit: contain;
    margin-left: 2.5%;
}
#item-right {
    height: 100%;
    width: 45%;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

@media (max-width: 900px) {
    #item-container {
        flex-direction: column;
        height: fit-content;
    }
    #item-img {
        width: 90%;
        margin-left: 5%;
    }
    #item-right {
        height: fit-content;
        width: 90%;
        margin-left: 5%;
        margin-bottom: 40px;
        margin-top: 30px;
    }
}
@media (min-width: 900px) {
    #item-container {
        flex-direction: row;
    }
    #item-img {
        width: 45%;
        margin-left: 2.5%;
    }
    #item-right {
        height: 100%;
        width: 45%;
        margin-left: 5%;
    }
}

#item-description {
    margin-top: 30px;
    width: 90%;
}

#item-right > p,
#item-right > div {
    font-family: "Lato", sans-serif;
    font-size: 16px;
}

#item-price {
    margin-top: 40px;
    font-size: 1.1em;
}

#item-interactive-content {
    margin-top: 30px;
}

#quantity-selector {
    margin-top: 10px;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
}

button {
    padding: 8px 20px;
    margin-left: 20px;
    background-color: white;
    border: 1px solid black;
    font-size: 16px;
    cursor: pointer;
}

#item-buy-loader {
    width: 30px;
    height: 30px;
    margin-left: 20px;
    margin-bottom: -15px;
    border: 5px solid rgb(206, 206, 206);
    border-top: 5px solid rgb(141, 141, 141);
    border-radius: 50%;
    animation: spin 2s ease-in-out infinite;
    display: none;
}

@keyframes spin {
    0% {
        rotate: 0deg;
    }
    100% {
        rotate: 360deg;
    }
}
