@import url("https://fonts.googleapis.com/css2?family=Gothic+A1:wght@500&family=Lato:wght@400;700&family=Roboto:wght@300;400;500;700;900&display=swap");

.cart-item {
    height: 100px;
    width: 80%;
    margin-bottom: 30px;
    padding: 10px;
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 6px;
    background-color: rgb(254, 254, 254);
    font-family: "Lato", sans-serif;
    font-size: 0.8em;
}
.cart-item-img {
    height: 100%;
    width: auto;
    border-radius: inherit;
    cursor: pointer;
}

.cart-item-info-container {
    margin: 0;
    padding: 10px;
    display: flex;
    justify-content: left;
}

@media (min-width: 525px) {
    .cart-item-info-container {
        flex-direction: row;
    }
    .cart-item-info-container > p {
        padding: 5px;
    }
}

@media (max-width: 525px) {
    .cart-item-info-container {
        flex-direction: column;
    }
    .cart-item-info-container > p {
        padding: 0;
    }
}

.cart-item > svg {
    position: absolute;
    right: 20px;
    cursor: pointer;
    font-size: 1em;
}
