#log-btn {
    margin-left: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    text-decoration: none;
    cursor: pointer;
    font-size: 20px;
}
a {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    text-decoration: none;
    font-size: 20px;
}

#log-btn > svg {
    margin-left: 5px;
    margin-top: 2px;
}

a > svg {
    margin-left: 5px;
    margin-top: 2px;
}
